.item
{
  background-color: var(--clr-background-muted);
  border-radius: 15px;
  box-shadow: -2px 2px 20px rgba(0, 0, 0, .18);
}

.header
{
  display: flex;
  padding-inline: .8em;
  padding-block: .7em;
  gap: .7em;
  align-items: center;
}

.title
{
  color: var(--clr-primary);
  font-size: 1.7rem;
  font-weight: 700;
}

.title:hover
{
  text-decoration: underline;
}

.button
{
  color: var(--clr-foreground);
  fill: var(--clr-foreground);
}

.body
{
  padding: 1em;
}
